<template>
    <div class="x-currency-monitor pa-3">
        <div v-if="coins && coins['USDBRL']">
            <div v-for="(coin, $index) of coins" :key="$index" class="x-coin">
                <v-card :color="coin.code === 'USD' ? 'primary' : (coin.code === 'EUR' ? 'success' : 'info')" dark elevation="0" class="mb-2">
                    <v-card-title class="text-body-2 justify-center pb-0">
                        <span class="mr-2"> {{ coin.code }} </span>
                        <v-icon size="20">mdi-arrow-right</v-icon>
                        <span class="ml-2"> {{ coin.codein }} </span>
                    </v-card-title>

                    <v-card-actions class="justify-center">
                        <div> {{ Intl.NumberFormat('pt-br', {style: 'currency', currency: 'BRL'}).format(coin.bid) }} </div>
                    </v-card-actions>
              </v-card>
            </div>

            <v-divider></v-divider>

            <div class="mt-2">
                <v-btn @click="load" color="primary" :loading="loading" depressed block> Atualizar Agora </v-btn>
            </div>
        </div>

        <div v-else>
            <p class="text-center red--text mt-4">Não foi possível obter a cotação das principais moedas.</p>
        </div>
    </div>
</template>

<script>
import { http, sleep } from '@/plugins/xtpgui';

export default {
    name: 'CurrencyMonitor',
    data() {
        return {
            loading: false,
            coins: {},
        }
    },
    methods: {
        async load() {
            try {
                this.loading = true;
                const response = await http('axios').get('https://economia.awesomeapi.com.br/json/last/USD-BRL,EUR-BRL,BTC-BRL');
                this.coins = response.data;
                await sleep(500);
            } catch (err) {
                console.log('[X] Não foi possível obter a cotação das principais moedas.');
            } finally {
                this.loading = false;
            }
        },
    },
    created() {
        this.load();
    },
}
</script>

